import React from 'react'
import {Drawer,Box, Typography, IconButton, Paper } from "@mui/material/"
import { useState } from 'react'
import MenuIcon from '@mui/icons-material/Menu';
const FlacDrawer1 = () => {
  const [isDrawerOpen, setIsDrawerOpen] = useState(false);
    return (
        <>
        <Paper sx={{paddingLeft:"10px",
                     display:"flex",
                     alignItems:"center",
                     width:'200px',
                     justifyContent:"space-between",
                    borderRadius:"15px"}}>
        <Typography sx={{marginLeft:"10px",fontWeight:"bold"}}>
            FLAC
        </Typography>
        <IconButton size='large' onClick={() => setIsDrawerOpen(true)}>
            <MenuIcon  />
        </IconButton>
        </Paper>
    <Drawer anchor="right" open={isDrawerOpen} onClose={() => setIsDrawerOpen(false)}>
        <Box p={2} width="250px" textAlign="center" >
        <Typography variant='h6' component='div'></Typography>
        Side Panel
        </Box>
    </Drawer>
    </>
  )
}
export default FlacDrawer1